body {
    width: 100%;
    height: 100%;
}

#inputCity {
    background-image: url('images/weatherLandscape2.jpg');
    background-attachment: local;
    background-repeat: no-repeat;
    background-position: top;
    overflow: hidden;
    background-size: cover;
    position: absolute;
    
}

.darkMode {
    background-color: #181e23;
    color: white;
    margin-top: 0;
    transition: .5s;
}
.darkmode #modeBtn {
    background-color: white;
    color: #181e23;
    transition: .5s;
}
.lightMode {
    background-color: #fff;
    color: #182027;
    transition: .5s;
}
.lightMode #modeBtn {
    background-color:#181e23;
    color:white;
    transition: .5s;
}
@media (max-width: 800px) {
    #title {
        position: relative; 
        display: block;
        width: 50%;
        margin: 0 auto 0 3rem;
        padding-top: 6rem;
    
    }
    #container {
        margin-top: 5rem;
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: minmax(400px, auto);
        position: relative;
        width: 80%;
        margin: 0 auto;
        padding-top: 5rem;
    }
    form {
        width: 100%;
        height: 150px;
        border-top-left-radius: 1.6rem;
        border-top-right-radius: 1.6rem;
        display: grid;
        justify-items: center;
    }

    form input {
        border: none;
        background-color: Transparent;
        border-bottom: 3px solid #0e3a57;
        margin-top: 1rem;
        color: white;
        font-size: 1.2rem;
        width: 40%;
    }
    form button {
        background-color: #0e3a57;
        border: none;
        color: white;
        cursor: pointer;
        border-radius: 5px;
        padding: .5rem 1rem !important;
        margin: 1rem;
        box-shadow: 0 7px 20px -10px rgba(150, 170, 180, 0.8);
    }

    #weatherWrapper {
        height: 300px;
        background-color: #f1eea1;
        padding: 5rem 0 2rem 0;
        border-bottom-left-radius: 1.6rem;
        border-bottom-right-radius: 1.6rem;
        box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.8);
    }
}


@media (min-width: 800px) {
    #title {
        position: relative; 
        display: block;
        width: 50%;
        margin: 0 auto 0 auto;
        padding-top: 6rem;
    }
    #container {
        position: relative;
        display: block;
        width: 50%;
        min-height: 18rem;
        margin: 3rem auto; 
    }
    form {
        position: absolute;
        float: left;
        width: 50%;
        height: 100%;
        padding: 2rem;
        border-top-left-radius: 3rem;
        border-bottom-left-radius: 3rem;
        box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.8);
    }
    form input {
        border: none;
        background-color: Transparent;
        border-bottom: 3px solid #0e3a57;
        margin-top: 3rem;
        color: white;
        font-size: 1.2rem;
        width: 40%;
    }
    form button {
        background-color: #0e3a57;
        border: none;
        color: white;
        cursor: pointer;
        border-radius: 5px;
        padding: .5rem 1rem;
        margin: 1rem;
        box-shadow: 0 7px 20px -10px rgba(150, 170, 180, 0.8);
    }
    #weatherWrapper {
        background-color: #f1eea1;
        position: absolute;
        float: right;
        width: 50%;
        height: 100%;
        margin-left: 50%;
        padding: 2rem;
        border-top-right-radius: 3rem;
        border-bottom-right-radius: 3rem;
        box-shadow: 0 7px 30px -10px rgba(150, 170, 180, 0.8);
    }
}